.current-meeting {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.current-meeting__title {
  font-size: 1.5rem;
}

.current-meeting__times {
  font-size: 2rem;
  margin-bottom: 3rem;
}

.current-meeting__remaining-text {
  font-size: 1.5rem;
}

.current-meeting__remaining-time {
  display: block;
  font-size: 5rem;
  margin-bottom: 3rem;
}

progress[value] {
  appearance: none;
  border: none;
  width: 70vw; height: 20px;
  background-color: whiteSmoke;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0,0,0,.5) inset;
  position: relative;
  margin: 0 0 1.5em; 
}

progress[value]::-webkit-progress-bar {
  background-color: whiteSmoke;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0,0,0,.5) inset;
}

progress[value]::-webkit-progress-value {
  position: relative;
  background-size: 35px 20px, 100% 100%, 100% 100%;
  border-radius:3px;
  animation: animate-stripes 5s linear infinite;
}

@keyframes animate-stripes { 100% { background-position: -100px 0; } }

progress[value]::-webkit-progress-value:after {
  content: '';
  position: absolute;

  width:5px; height:5px;
  top:7px; right:7px;

  background-color: white;
  border-radius: 100%;
}

progress[value]::-moz-progress-bar {

background-image:

-moz-linear-gradient( top,
                        rgba(255, 255, 255, .25),
                        rgba(0,0,0,.2)),
 -moz-linear-gradient( left, #09c, #f44);

background-size: 35px 20px, 100% 100%, 100% 100%;
border-radius:3px;
}

.progress-bar {
  background-color: whiteSmoke;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0,0,0,.5) inset;
  width: 70vw; height:20px;
}

.progress-bar span {
  background-color: royalblue;
  border-radius: 3px;
  display: block;
  text-indent: -9999px;
}
